<script>
    import chroma from 'chroma-js';
    import { beforeUpdate, onMount } from 'svelte';
    import Checkbox from './Checkbox.svelte';
    import InputColors from './InputColors.svelte';
    import PalettePreview from './PalettePreview.svelte';
    import Export from './Export.svelte';
    import StepChart from './StepChart.svelte';
    import Card from './Card.svelte';
    import ColorBlindCheck from './ColorBlindCheck.svelte';
    import ButtonGroup from './ButtonGroup.svelte';

    export let name;

    let steps = [];
    let bezier = true;
    let correctLightness = true;

    let colors = '00429d,96ffea,lightyellow'.split(/\s*,\s*/).map(c => chroma(c));
    let colors2 = 'ffffe0,ff005e,93003a'.split(/\s*,\s*/).map(c => chroma(c));
    let numColors = 9;
    let mode = 'sequential';
    let simulate = 'none';

    if (window.location.hash) {
        readStateFromHash();
    }

    $: hash = [
        numColors,
        mode.substr(0,1),
        colors.map(c=>c.hex().substr(1)).join(','),
        colors2.length ? colors2.map(c=>c.hex().substr(1)).join(',') : '',
        correctLightness ? 1:0,
        bezier?1:0
    ].join('|');

    const isMac = navigator.platform.toUpperCase().indexOf('MAC') > -1;

    let _hash = '';
    let _mounted = false;
    let _mode = 'sequential';

    beforeUpdate(() => {
        if (hash !== _hash) {
            _hash = hash;
            window.location.hash = `#/${hash}`;
        }
        if (mode !== _mode) {
            if (mode === 'diverging' && !colors2.length) {
                colors2 = colors.slice(0).reverse();
            }
            _mode = mode;
        }
    });

    // onMount(() => {
    //     if (window.location.hash) {
    //         console.log('initial hash', window.location.hash);
    //         readStateFromHash();
    //     }
    //     _mounted = true;
    // })

    function readStateFromHash() {
        const parts = window.location.hash.substr(2).split('|');
        if (parts.length === 6) {
            setTimeout(() => {
                numColors = +parts[0];
                mode = parts[1] === 's' ? 'sequential' : 'diverging';
                _mode = mode;
                colors = parts[2].split(',').map(c => c && chroma(c));
                colors2 = parts[3] !== '' ? parts[3].split(',').map(c => c && chroma(c)) : [];
                correctLightness = parts[4] === '1';
                bezier = parts[5] === '1';
            })
        } else {
            window.location.hash = '';
        }
    }

    function hashChange() {
        if (window.location.hash !== `#/${hash}`) {
            // deserialize hash
            readStateFromHash();
        }
    }
</script>

<style>
    .head {
        margin: 1em 0 1em;
    }
    h1 {
    }
    select.custom-select {
        display: inline-block;
        width: auto;
        font-size: inherit;
        padding: 0.4em 1.7em 0.4em 0.4em;
        margin: 0px 0.7ex 5px;
    }
    input[type=number] {
        width: 4em;
        text-align: center;
        margin: 0px 0.7ex 5px;
    }
    .foot {
        margin-bottom: 1em;
    }
    :global(.fa-svelte) {
        vertical-align: sub;
    }
    kbd
    {
        -moz-border-radius:3px;
        -moz-box-shadow:0 1px 0 rgba(0,0,0,0.2),0 0 0 2px #fff inset;
        -webkit-border-radius:3px;
        -webkit-box-shadow:0 1px 0 rgba(0,0,0,0.2),0 0 0 2px #fff inset;
        background-color:#f7f7f7;
        border:1px solid #ccc;
        border-radius:3px;
        box-shadow:0 1px 0 rgba(0,0,0,0.2),0 0 0 2px #fff inset;
        color:#333;
        display:inline-block;
        /*font-family:Arial,Helvetica,sans-serif;*/
        line-height:1.4;
        margin:0 .1em;
        padding:.1em .6em;
        text-shadow:0 1px 0 #fff;
    }
</style>

<svelte:window on:hashchange={hashChange} />

<div class="container">
    <div class="head">
        <h1>Для любимой</h1>
    </div>
    <Card step="1" title="Какую палитру вы хотите создать?">
        <div class="row">
            <div class="col-md">
                Тип палитры:
                <ButtonGroup options="{['sequential', 'diverging']}" bind:value={mode} />
            </div>
            <div class="col-md">
                Кол-во цветов: <input type="number" min="2" bind:value={numColors} />
            </div>
        </div>
    </Card>

    <Card step="2" title="Выбери цвет, кися">
        <InputColors diverging="{mode==='diverging'}" bind:colors bind:colors2 />
    </Card>

    <Card step="3" title="Просмотр и настройка сгенерированных цветов">
        <div class="row" style="margin-bottom: 10px">
            <div class="col-md">
                <Checkbox bind:value={correctLightness} label="correct lightness" />
                <Checkbox bind:value={bezier} label="bezier interpolation" />
            </div>
            <div class="col-md">
                <ColorBlindCheck bind:colors={steps} bind:active={simulate} />
            </div>
        </div>
        <PalettePreview
            bind:steps
            bind:correctLightness
            bind:bezier
            bind:colors
            bind:colors2
            diverging="{mode === 'diverging'}"
            simulate={simulate}
            bind:numColors />
    </Card>

    <Card step="4" title="Цветовые коды">
        <Export steps={steps} />
    </Card>
</div>